<template>
	<div class="wrapper">
		<mu-alert color="error" v-if="stoping" style="z-index: 20151225;position: absolute;width: 1300px;">
			<mu-icon left value="priority_high"></mu-icon>Pausing
		</mu-alert>
		<div class="your-ships">
			<h3>Your Ships:</h3>
			<div class="ships">
				<div v-for="(ship,idx) in config.ships" :key="idx" class="ship-type">
					{{ship}} &nbsp;&nbsp; ({{shipNums[ship].num}}/{{shipNums[ship].max}})
					<!-- <div v-for="n in config.shipNums[ship]" :key="n" class="ship-items"> -->
					<Grid
						:x="+shipSize[ship]"
						:y="1"
						color="#2196f3"
						@pick="() => handlePick(ship,+shipSize[ship])"
					></Grid>
				</div>
			</div>
		</div>
		<div class="action">
			<div class="curr-ship" :class="{'no-click': isReady || isStart}">
				<div class="grid">
					{{currShipName}}
					<Grid :x="currShipSize.x" :y="currShipSize.y" color="#ff4081"></Grid>
				</div>
				<div class="direction">
					<mu-icon class="icon" value="arrow_back" size="60" @click="setDir('up')"></mu-icon>
					<mu-icon
						class="icon"
						value="arrow_back"
						size="60"
						style="transform: rotate(-90deg);"
						@click="setDir('down')"
					></mu-icon>
					<mu-icon
						class="icon"
						value="arrow_back"
						size="60"
						style="transform: rotate(90deg);"
						@click="setDir('left')"
					></mu-icon>
					<mu-icon
						class="icon"
						value="arrow_back"
						size="60"
						style="transform: rotate(180deg);"
						@click="setDir('right')"
					></mu-icon>
					<div class="btns">
						<mu-button color="secondary" @click="rotate" :disabled="!currShip.length">
							ROTATE
							<mu-icon right value="rotate_90_degrees_ccw"></mu-icon>
						</mu-button>&nbsp;&nbsp;&nbsp;
						<mu-button color="success" @click="handleOk" :disabled="!currShip.length">
							Confirm
							<mu-icon right value="done"></mu-icon>
						</mu-button>
						<!-- <mu-button color="primary" @click="handleStart">Start</mu-button> -->
					</div>
				</div>
			</div>
			<div class="btns-right">
				<mu-button round large class="start" @click="ready" :disabled="isStart || !shipsFlat.length">
					Ready
					<mu-icon right value="done_all"></mu-icon>
				</mu-button>
				<mu-button
					v-if="role === 'homeowner'"
					color="green"
					round
					large
					class="start"
					:disabled="!allReady || isStart"
					@click="start"
				>
					Start
					<mu-icon right value="send"></mu-icon>
				</mu-button>

				<mu-button color="red" fab @click="pause" :disabled="!isStart">
					<mu-icon :value="stoping ? 'play_arrow' : 'pause'"></mu-icon>
				</mu-button>
				<div class="quit">
					<mu-button color="red" large @click="quit">
						Quit
						<mu-icon right value="exit_to_app"></mu-icon>
					</mu-button>
				</div>

				<!-- <mu-button color="success" @click="handleOk">OK</mu-button> -->
			</div>
		</div>
		<div class="grids">
			<Grid
				:x="+config.gridSize"
				:y="+config.gridSize"
				:currShip="currShip"
				:ships="shipsFlat"
				:shootIn="myShootIn"
				:shootOut="myShootOut"
				:width="450"
			></Grid>
			<mu-button color="orange" @click="test" style="transform: translateY(-20px);">
				<mu-icon value="arrow_back" left></mu-icon>You
			</mu-button>
			<mu-button color="orange" style="transform: translateY(20px);">
				Enemy
				<mu-icon value="arrow_forward" right></mu-icon>
			</mu-button>

			<Grid
				v-loading="isStart && waiting"
				:data-mu-loading-text="loadText"
				ref="xxx"
				:x="+config.gridSize"
				:y="+config.gridSize"
				:shootIn="enemyShootIn"
				:shootOut="enemyShootOut"
				@cellClick="handleCellClick"
				:width="450"
			></Grid>
		</div>
	</div>
</template>
<script>
import Grid from '@/components/Grid'
import VueSocketIO from 'vue-socket.io'
import SocketIO from 'socket.io-client';
import Vue from 'vue'
import store from '../store'

Vue.use(new VueSocketIO({
	debug: false,
	connection: SocketIO('http://localhost:3001?roomId=' + sessionStorage.getItem('currRoom')),
	// connection: SocketIO('http://localhost:3001/' + sessionStorage.getItem('currRoom')),
	vuex: {
		store,
		actionPrefix: 'SOCKET_',
		mutationPrefix: 'SOCKET_'
	},
}))

export default {
	sockets: {
		connect() {
			console.log('socket connected')
		},
		message(msg) {
			const { action, data } = msg
			switch (action) {
				case 'allReady':
					this.allReady = true
					break;
				case 'start':
					this.$toast.success('GAME START!!!')
					this.isStart = true
					this.currPlayer = 'homeowner'

					break;
				case 'pause':
					this.stoping = data
					if (this.stoping) {
						this.loadText = 'Pausing'
					}
					break;
				case 'quit':
					this.alert('Your Enemy quit!', 'GAME OVER!!!', true)
					this.isStart = false
					this.stoping = false
					break;
				case 'game_isShoot':
					var { shoot, xy, over, winner, nextPlayer } = data
					if (shoot) {
						this.enemyShootIn.push(xy)
					} else {
						this.enemyShootOut.push(xy)
					}
					if (over) {
						this.isStart = false
						if (winner === this.role) {
							this.alert('You win and get 1 score!', 'GAME OVER!!!', true)
						} else {
							this.setGameRecord(false)
							this.alert('You lose!', 'GAME OVER!!!', true)
						}
					} else {
						this.currPlayer = nextPlayer
					}
					break;
				case 'game_shooted':
					var { shoot, xy, over, winner, nextPlayer } = data
					if (shoot) {
						this.myShootIn.push(xy)
					} else {
						this.myShootOut.push(xy)
					}
					if (over) {
						this.isStart = false
						if (winner === this.role) {
							this.alert('You win and get 1 score!', 'GAME OVER!!!', true)
						} else {
							this.setGameRecord(false)
							this.alert('You lose!', 'GAME OVER!!!', true)
						}
					} else {
						this.currPlayer = nextPlayer
					}
					break;
				default:
					break;
			}
		}
	},
	components: { Grid },
	data() {
		return {
			loadText: 'Waiting for your enemy',
			currPlayer: '',
			config: {},
			isReady: false,
			shipSize: {
				'AircraftCarrier': 5,
				'Battleship': 4,
				'Cruiser': 3,
				'Destroyer': 2,
				'Submarine': 1,
			},
			currShip: [],
			isStart: false,
			currShipName: '',
			currShipSize: {
				x: 0,
				y: 0
			},
			ships: [],
			shipNums: {},
			roomId: '',
			role: '',
			allReady: false,
			myShootIn: [],
			myShootOut: [],
			enemyShootIn: [],
			enemyShootOut: [],
			stoping: false
		}
	},
	computed: {
		waiting() {
			const { currPlayer, role } = this
			return currPlayer !== role
		},
		shipsFlat() {
			const value = this.ships
			const arr = []
			value.forEach(v => {
				arr.push(...v)
			})
			return arr
		}
	},
	created() {
		this.roomId = this.$route.query.id
		this.role = this.$route.query.role
		this.config = JSON.parse(sessionStorage.getItem('config'))
		console.log(this.config)
		const shipNums = {}
		this.config.ships.forEach(v => {
			shipNums[v] = {
				num: 0,
				max: this.config.shipNums[v]
			}
		})
		this.shipNums = shipNums

		if (this.role === 'enemy') {
			this.$http({
				method: 'post',
				url: '/rooms/setEnemy',
				data: {
					id: this.roomId,
					name: sessionStorage.getItem('user')
				}
			})
		}
	},
	mounted() {
		if (!JSON.parse(sessionStorage.getItem('hasGuide'))) {
			this.guide()
		}
	},
	methods: {
		setGameRecord(win) {
			this.$http({
				url: '/users/setGameRecord',
				method: 'post',
				data: {
					win,
					user: sessionStorage.getItem('user'),
					time: new Date()
				}
			})
		},
		alert(text, title, quit) {
			this.$alert(text, title, {
				okLabel: 'OK'
			}).then(() => {
				if (quit) {
					this.$http({
						url: '/rooms',
						method: 'delete',
						data: {
							id: this.roomId
						}
					}).then(() => {
						this.$router.push('/rooms')
					})
				}
			});
		},
		quit() {
			this.$confirm('Are you sure to quit?', {
				type: 'warning'
			}).then(({ result }) => {
				if (result) {
					this.$socket.emit('message', {
						action: 'quit',
						data: {
							id: this.roomId
						}
					})
					setTimeout(() => {
						this.$router.push('/rooms')
					}, 1500)
				} else {

				}
			});

		},
		pause() {
			this.stoping = !this.stoping
			this.$socket.emit('message', {
				action: 'pause',
				data: {
					pause: this.stoping,
					id: this.roomId
				}
			})
		},
		start() {
			this.$socket.emit('message', {
				action: 'start',
				data: {
					id: this.roomId
				}
			})
		},
		ready() {
			this.isReady = true
			this.$http({
				url: '/rooms/setShips',
				method: 'post',
				data: {
					id: this.roomId,
					ships: this.ships,
					role: this.role
				}
			}).then(() => {
				this.$socket.emit('message', {
					action: 'ready',
					data: {
						player: sessionStorage.getItem('user'),
						id: this.roomId
					}
				})
			})
		},
		test() {
			this.$socket.emit('test', 'hello socket')
		},
		guide() {
			//initialize instance
			var enjoyhint_instance = new EnjoyHint({
				onStart: function () {
					sessionStorage.setItem('hasGuide', 'true')
				}
			});

			//simple config. 
			//Only one step - highlighting(with description) "New" button 
			//hide EnjoyHint after a click on the button.
			var enjoyhint_script_steps = [
				{
					'next .ships': 'Select the class of ship'
				},
				{
					'next .direction': 'Set the position of your ship'
				},
				{
					'skip .start': 'Enjoy yourself!'
				}
			];

			//set script config
			enjoyhint_instance.set(enjoyhint_script_steps);

			//run Enjoyhint script
			enjoyhint_instance.run();
		},
		handleCellClick({ row, col }) {
			if (this.stoping) {
				return
			}
			this.$socket.emit('message', {
				action: 'game',
				data: {
					xy: `${row}${col}`,
					id: this.roomId,
					role: this.role,
					user: sessionStorage.getItem('user')
				}
			})
		},
		handleStart() {

		},
		handleOk() {
			if (this.currShip.some(v => this.shipsFlat.includes(v))) {
				this.$toast.error('Overlap!')
				return
			}
			this.ships.push(this.currShip)
			this.shipNums[this.currShipName].num = this.shipNums[this.currShipName].num + 1

			this.currShip = []
			this.currShipName = ''
			this.currShipSize = {
				x: 0,
				y: 0
			}
		},
		handlePick(ship, size) {
			if (this.isReady) {
				return
			}
			if (this.shipNums[ship].num === this.shipNums[ship].max) {
				this.$toast.error(`The number of ${ship} has reached its maximum!`)
				return
			}
			this.currShipName = ship
			this.currShipSize = {
				x: size,
				y: 1
			}
			const arr = []
			for (let i = 0; i < size; i++) {
				arr.push('0' + i)
			}
			this.currShip = arr
		},
		setDir(dir) {
			const size = this.config.gridSize - 1
			const arr = this.currShip
			const xy = arr.map(v => ({
				x: +v[0],
				y: +v[1]
			}))
			switch (dir) {
				case 'up':
					xy.forEach(v => v.y = v.y - 1)
					break;
				case 'right':
					xy.forEach(v => v.y = v.y + 1)
					break;
				case 'left':
					xy.forEach(v => v.x = v.x - 1)
					break;
				case 'down':
					xy.forEach(v => v.x = v.x + 1)
					break;
				default:
					break;
			}
			if (xy.some(v => v.x < 0 || v.y < 0 || v.x > size || v.y > size)) {
				this.$toast.error('Out!');
				return
			}
			const ret = xy.map(v => `${v.x}${v.y}`)
			this.currShip = ret
		},
		rotate() {
			let size = 0
			let arr = []
			if (this.currShipSize.x === 1) {
				size = this.currShipSize.y
				this.currShipSize = {
					x: size,
					y: 1
				}
				for (let i = 0; i < size; i++) {
					arr.push('0' + i)
				}

			} else {
				size = this.currShipSize.x
				this.currShipSize = {
					y: size,
					x: 1
				}
				for (let i = 0; i < size; i++) {
					arr.push(i + '0')
				}

			}
			// console.log(arr)
			this.currShip = arr
		}
	}
}
</script>
<style lang="less" scoped>
.wrapper {
	padding: 50px;
	width: 1300px;
	margin: 0 auto;
}
.grids {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	padding-top: 50px;
	// width: 1000px;
	margin: 0 auto;
	align-items: center;
	padding: 20px 7px;
}
.ships {
	padding: 0 7px;
	display: flex;
	justify-content: space-between;

	.ship-type {
		margin-bottom: 20px;
		margin-right: 50px;
	}
}
.action {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.curr-ship {
	width: 800px;
	// margin: 0 auto;
	height: 250px;
	box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
		0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
	padding: 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	> .grid {
		width: 400px;
	}
}
.icon {
	cursor: pointer;
}
.your-ships {
	box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2),
		0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
	padding: 15px;
	margin-bottom: 20px;
}
.quit {
	position: absolute;
	left: 50px;
	top: 66px;
}
.btns-right {
	display: flex;
	flex-direction: column;
	height: 250px;
	justify-content: space-around;
}
.no-click {
	pointer-events: none;
	cursor: not-allowed;
}
</style>


