<template>
	<div
		@click.stop="$emit('pick')"
		:style="{width: width ? width + 'px' : 'auto',height: width ? width + 'px' : 'auto'}"
		:class="{fixedWidth: !!width}"
	>
		<div v-for="(n,row) in y" :key="n" class="row">
			<div
				v-for="(n,col) in x"
				:key="n"
				class="cell"
				@click="handleClick(row,col)"
				:style="{background: color,borderColor: color === 'transparent' ? '#ddd' : color,width: width ? (width / x) + 'px' : '40px',height: width ? (width / y) + 'px' : '40px'}"
				:class="{currShip: currShip.includes(`${row}${col}`),ships: ships.includes(`${row}${col}`)}"
			>
				<!-- {{`${row}${col}`}} -->
				<mu-icon value="block" v-if="shootOut.includes(`${row}${col}`)"></mu-icon>
				<mu-icon value="close" color="red" v-if="shootIn.includes(`${row}${col}`)"></mu-icon>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		x: {
			type: Number,
			default: 10
		},
		y: {
			type: Number,
			default: 10
		},
		color: {
			type: String,
			default: 'transparent'
		},
		currShip: {
			type: Array,
			default: () => []
		},
		ships: {
			type: Array,
			default: () => []
		},
		shootIn: {
			type: Array,
			default: () => []
		},
		shootOut: {
			type: Array,
			default: () => []
		},
		width: Number
	},
	methods: {
		handleClick(row, col) {
			if (this.shootOut.includes(`${row}${col}`) || this.shootIn.includes(`${row}${col}`)) {
				return
			}
			this.$emit('cellClick', { row, col })
		}
	}
}
</script>

<style scoped lang="less">
.row {
	display: flex;
}

.cell {
	width: 40px;
	height: 40px;
	border: 1px solid #ddd;
	cursor: pointer;
	transition: all 0.3s;
	display: flex;
	justify-content: center;
	align-items: center;
	// &:hover {
	// 	background: #ddd;
	// }
}
.color,
.ships {
	background: #2196f3 !important;
	border-color: #2196f3 !important;
}
.currShip {
	background: #ff4081 !important;
	opacity: 0.5;
	border-color: #ff4081 !important;
}
// .fixedWidth {
//     display: flex;
// 	.cell {
//         flex: 1;
//         height: ;
// 	}
// }
</style>
